.home {
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.75)), url("../resources/test2.png") ;
}


.blinking{
  animation:blinkingText 1.2s infinite;
  -webkit-animation :blinkingText 1.2s infinite;
}

@keyframes blinkingText{
  0%{     color: rgb(221, 11, 240);    }
  49%{    color: rgb(46, 236, 20); }
  60%{    color: rgb(0, 174, 255); }
  99%{    color:  transparent;  }
  100%{   color: #FFF;    }
}

@-webkit-keyframes blinkingText{
  0%{     color: rgb(221, 11, 240);    }
  49%{    color: rgb(46, 236, 20); }
  60%{    color: rgb(0, 174, 255); }
  99%{    color:  transparent;  }
  100%{   color: #FFF;    }
}


.homepage-header {
  text-align: center;
  font-family: Avant Garde,Avantgarde,Century Gothic,CenturyGothic,AppleGothic,sans-serif;
  font-size: 5vw;
  transition: opacity 4s;
  opacity: 1;
 color: rgba(190, 190, 190, 1);
}
.homepage-sub {
  text-align: center;
  font-family: Avant Garde,Avantgarde,Century Gothic,CenturyGothic,AppleGothic,sans-serif;
  font-size: 2vw;
  font-style: italic;
  color: rgba(190, 190, 190, 1);
}

.homepage-button {
  background-color: #4CAF50; /* Green */
  outline: none;
  color: white;
  width: 200px !important;
  padding: 12px 32px;
  text-align: center;
  display: inline-block;
  font-size: 18px;
  margin: 16px 2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  font-family: "Courier New", Courier, monospace;
  background-color: rgba(0, 0, 0, 0.0);
  color: rgba(190, 190, 190, 1);
  border: 2px solid rgba(190, 190, 190, 1);
}

.homepage-button:hover {
  background-color: rgba(163, 29, 95, 0.5);
  color: rgba(190, 190, 190, 1);
  transform: scale(1.2);
}
.homepage-button:active {
  transform: scale(.9);
}

.fade-in {
  animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s;
}

@keyframes fadeIn{
  from {
     opacity:0;
     margin-top: 10%
   }
   to {
     opacity:1;
     margin-top: 1000
   }
}

@-webkit-keyframes fadeIn {
  from {
     opacity:0;
     margin-top: 10%
   }
   to {
     opacity:1;
     margin-top: 1000
   }
}
